h1 {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

@keyframes jello-horizontal {
  0% {
    transform: scale3d(1, 1, 1);
  }

  30% {
    transform: scale3d(1.25, .75, 1);
  }

  40% {
    transform: scale3d(.75, 1.25, 1);
  }

  50% {
    transform: scale3d(1.15, .85, 1);
  }

  65% {
    transform: scale3d(.95, 1.05, 1);
  }

  75% {
    transform: scale3d(1.05, .95, 1);
  }

  100% {
    transform: scale3d(1, 1, 1);
  }
}

@keyframes rotate-90-cw {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(90deg);
  }
}

@font-face {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  src: url("Roboto-Regular-webfont.55e67705.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

.page {
  scroll-behavior: smooth;
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.page__body {
  background: #eef7ff;
  min-width: 320px;
  margin: 0;
  font-family: Inter, sans-serif;
  transition: all .3s ease-in-out;
}

.page__menu {
  z-index: 1;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

.page__switcher-color-palette {
  z-index: 1;
  background: linear-gradient(0deg, #f7f3fe 0%, #ebf0ff 100%);
  border: 1px solid #ffeefc;
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  width: 30px;
  height: 80px;
  padding: 8px 8px 0;
  transition: all .5s;
  display: flex;
  position: fixed;
  top: 20%;
  right: -10%;
  transform: translateY(-50%);
  box-shadow: 0 0 15px #0003;
}

@media (width <= 1280px) {
  .page__switcher-color-palette {
    display: none;
  }
}

.page__switcher-button {
  z-index: 1;
  background: url("Icon-arrow.e9359bd2.svg") center / cover;
  border: none;
  width: 30px;
  height: 12px;
  transition: transform .5s ease-in-out;
  position: fixed;
  top: 20%;
  right: .3%;
  transform: rotate(180deg);
}

.page__switcher-button:hover {
  cursor: pointer;
}

@media (width <= 1280px) {
  .page__switcher-button {
    display: none;
  }
}

.page__switcher--visible {
  right: 2.5%;
}

